import React from 'react'
import HomeLoader from '@/components/molecules/home-loader.compornent'
import PageLoader from '@/components/molecules/page-loader.compornent'
import Header from '@/components/organisms/site-header.component'
import Footer from '@/components/organisms/site-footer.component'
import {
  TransitionGroup,
  Transition as ReactTransition,
} from 'react-transition-group'
import styles from './layout.module.scss'

// NOTE: product-index.template .
const motion = {
  timeout: 500, // .
  delay: 200, // .
}

const transitionStyles = {
  move: {
    // .
    entering: {
      opacity: 0,
      transform: `translate(0, 20px)`,
    },
    //  .
    entered: {
      transition: `transform ${motion.timeout}ms ease-out,
                   opacity ${motion.timeout}ms ease-in`,
      opacity: 1,
      transform: `translate(0, 0)`,
    },
    // .
    exiting: {
      transition: `opacity ${motion.timeout}ms ease-in`,
      opacity: 0,
      transform: `translate(0, 0)`,
    },
    // .
    exited: {
      opacity: 0,
      transform: `translate(0, 20px)`,
    },
  },
  fade: {
    entering: {
      transition: `opacity ${motion.timeout}ms ease-in`,
      opacity: 1,
    },
    entered: {},
    exiting: {
      transition: `opacity ${motion.timeout}ms ease-in`,
      opacity: 0,
    },
    exited: {
      opacity: 0,
    },
  },
}

/**
 * 
 *
 * @param  {{location: object, children: object, isPageType: string}} props
 * @return {JSX.Element}
 */
const Layout = ({ location, children, isPageType }) => {
  const isHome = isPageType === 'Home' ? true : false

  // Home.
  if (isHome) {
    return (
      <div className={`${styles.site} ${styles.pageHome}`}>
        <HomeLoader />
        <TransitionGroup>
          <ReactTransition
            key={location.pathname}
            appear={true}
            timeout={{
              enter: motion.timeout,
              exit: motion.timeout,
            }}
          >
            {status => (
              <div
                style={{
                  ...transitionStyles.fade[status],
                }}
              >
                <div className={styles.site__sidebar}>
                  <Header location={location} position="sidebar" />
                </div>
                <div className={styles.site__content}>
                  <div className={styles.site__content__in}>
                    <main>{children}</main>
                  </div>
                </div>
              </div>
            )}
          </ReactTransition>
        </TransitionGroup>
      </div>
    )
  }

  // Home.
  return (
    <div className={styles.site}>
      <PageLoader />
      <div className={styles.site__sidebar}>
        <Header location={location} position="sidebar" />
      </div>
      <div className={styles.site__content}>
        <div className={styles.site__content__in}>
          <Header location={location} position="content" />
          <TransitionGroup>
            <ReactTransition
              key={location.pathname}
              appear={true}
              timeout={{
                enter: motion.timeout,
                exit: motion.timeout,
              }}
            >
              {status => (
                <div
                  style={
                    /* Product */
                    isPageType === 'Product'
                      ? {}
                      : { ...transitionStyles.move[status] }
                  }
                >
                  <main>{children}</main>
                </div>
              )}
            </ReactTransition>
          </TransitionGroup>
          <Footer location={location} position="content" />
        </div>
      </div>
    </div>
  )
}

export default Layout
