import React, { useState, useEffect, useContext } from 'react'
import styled from '@emotion/styled'
import { Backdrop } from '@mui/material'
import { Typography, Box, LinearProgress } from '@mui/material'
import { LoadedContext } from '@/story/contexts'

// 
const transition = {
  appear: 0,
  enter: 0,
  exit: 1000,
}

/**
 * 
 *
 * @return {JSX.Element}
 */
const HomeLoader = () => {
  // 
  const [loadedState, loadedDispatch] = useContext(LoadedContext)

  // 
  const [fadeOn, setFadeOn] = useState(true)

  // 
  const [progress, setProgress] = useState(loadedState?.is ? 50 : 0)

  // .
  useEffect(() => {
    let timer = 0

    // .
    if (!loadedState?.is) {
      loadedDispatch({ type: 'end' })
    } else {
      /* NOTE: 2.
      return () => {
        clearInterval(timer)
      }
      */
    }

    timer = setInterval(() => {
      setProgress(prevProgress => {
        switch (prevProgress) {
          case 100:
            setFadeOn(() => false)
            clearInterval(timer)
            return 100
          case 50:
            return prevProgress + 1
          default:
            return prevProgress + 1
        }
      })
    }, 20)
  }, [loadedState, loadedDispatch])

  // 
  const StyledBackdrop = styled(Backdrop)`
    background: #111;
    color: #f1f1f1;
    z-index: 100;
  `
  const StyledLinearBox = styled(Box)`
    width: 100%;
    border-radius: 10px;
    overflow: hidden;
  `
  const StyledLinearProgress = styled(LinearProgress)`
    background: #b7d4d0;

    .MuiLinearProgress-bar {
      background: #33b2a6;
    }
  `

  return (
    <StyledBackdrop open={fadeOn} transitionDuration={transition}>
      <Box
        display="flex"
        justifyContent="center"
        alignItems="center"
        width="100%"
      >
        <StyledLinearBox maxWidth={160} ml={1}>
          <StyledLinearProgress variant="determinate" value={progress} />
        </StyledLinearBox>
        <Box minWidth={40} textAlign="right" mr={1}>
          <Typography variant="body2">{`${Math.round(progress)}%`}</Typography>
        </Box>
      </Box>
    </StyledBackdrop>
  )
}
export default HomeLoader
